import React from 'react';
import Tabs from "./Tabs"; 
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLightbulb, faPencilRuler, faGlobeEurope } from '@fortawesome/free-solid-svg-icons';
import './ToggleTabs.scss';

function ToggleTabs() {
  const belowLabelStyle = {
    paddingTop: '10px'
  }
  return (
    <div>
      <Tabs> 
       <div label="Beginning" >
         <p style={belowLabelStyle}> All starts from the idea. On the very beginning we dealt with ... </p>
         <FontAwesomeIcon style={{fontSize: '2.2em', opacity: '0.6'}} icon={faLightbulb} />
       </div> 
       <div label="Currently"> 
         <p style={belowLabelStyle}> We developed ourselves well and currently we are dealing with ... </p>
         <FontAwesomeIcon style={{fontSize: '2.2em', opacity: '0.6'}} icon={faPencilRuler} />
       </div> 
       <div label="Future"> 
         <p style={belowLabelStyle}> We are going to get on the new market with our product in order to ...</p>
         <FontAwesomeIcon style={{fontSize: '2.2em', opacity: '0.6'}} icon={faGlobeEurope} />
       </div> 
     </Tabs> 
    </div>
  );
}

export default ToggleTabs;