import React, { useState } from 'react';
import { motion } from 'framer-motion';
// import { Route, Switch } from 'react-router-dom'; import LikeDislike from '../LikeDislike/LikeDislike'; import ReactTooltip from 'react-tooltip'; import Circle from '../Motions/Circle'; import CustomDropdown from "../CustomDropdown/CustomDropdown";
import Note from "./Note";
import CreateArea from "./CreateArea";
import './NotesKeeper.scss';
import '../../styles/main.scss';


function NotesKeeper() {
  const [notes, setNotes] = useState([]);

  function addNote(newNote) {
    setNotes(prevNotes => {
      return [...prevNotes, newNote];
    });
  }

  function deleteNote(id) {
    setNotes(prevNotes => {
      return prevNotes.filter((noteItem, index) => {
        return index !== id;
      });
    });
  }

  return (
    <div>
      <motion.div className='wrapper-middle'
        initial={{opacity: 0}}
        animate={{opacity: 1}}
        exit={{opacity: 0}}
      >
        <div className='notes'>
          {/* <Header /> */}
          <h1 className='how-it-started'>Notes</h1>
          <CreateArea onAdd={addNote} />
          {notes.map((noteItem, index) => {
            return (
              <Note
                key={index}
                id={index}
                title={noteItem.title}
                content={noteItem.content}
                onDelete={deleteNote}
              />
            );
          })}
          {/* <Footer /> */}
        </div>
      </motion.div>
    </div>
  );
}

export default NotesKeeper;
