import React, { useState } from "react";
//import DeleteIcon from "@material-ui/icons/Delete";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheckCircle, faTrash } from '@fortawesome/free-solid-svg-icons';


function Note(props) {
  const [check, setCheck] = useState(false);
  function handleClick() {
    props.onDelete(props.id);
  }
  function handleClickCheck() {
    setCheck(!check);
  }
  let checkButtonClass = check ? "button-check-activated" : "button-check-deactivated";
  let checkHeaderClass = check ? "inactive" : "";
  let checkParagraphClass = check ? "inactive" : "";
  
  return (
    <div className="note">
      <h1 className={checkHeaderClass}>{props.title}</h1>
      <p className={checkParagraphClass}>{props.content}</p>
      <button onClick={handleClick}>
        {/* <DeleteIcon /> */}
        <FontAwesomeIcon icon={faTrash} />
      </button>
      <button className={checkButtonClass} onClick={handleClickCheck}>
        <FontAwesomeIcon icon={faCheckCircle} />
      </button>
    </div>
  );
}

export default Note;
